<template>
<div id="main-btn">
 <button v-on:click="switmake()" > Законы</button>
 <button v-on:click="swdoc()"> Документы </button>
</div>

  <HelloWorld msg="Welcome to Your Vue.js App" v-if=make />
  <documents v-if=doc />
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import documents from './components/documents.vue'

export default {
  name: 'App',
  components: {
    HelloWorld,
    documents
  },
  data() {
    return {
      make:true,
      doc:false 
    }
  },
  methods:{
    switmake:function() {
      this.make=true
      this.doc=false
    },
    swdoc:function() {
      this.doc=true
      this.make=false
    }

  }
}

  

</script>

